<template>
  <div>
    <p></p>
    <div class="addUnion" v-if="canEdit">
      <el-button type="primary" @click="addNonErpProject"></el-button>
    </div>
    <!-- ERP  -->
    <div id="noUnion" v-if="nonErpTextList.length === 0">
      <img src="~assets/images/noCurrent.png">
      <p>
        
      </p>
    </div>
    <!-- ERP  -->
    <el-table v-if="nonErpTextList.length > 0" :data="nonErpTextList" style="width: 100%" max-height="450" id="table1" v-show="canEdit">
      <el-table-column prop="name" label="">
        <template slot-scope="scope">
          <el-input v-model="scope.row.name" placeholder="" @change="nonErpTextListChange"></el-input>
        </template>
      </el-table-column>
      <el-table-column prop="number" label="">
        <template slot-scope="scope">
          <el-input v-model="scope.row.number" placeholder=""  @keyup.native="check(scope)" @change="nonErpTextListChange"></el-input>
        </template>
      </el-table-column>
      <el-table-column prop="" label="" width="180">
        <template slot-scope="scope">
          <el-button size="small" @click="handleDelete(scope)"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-table v-if="nonErpTextList.length > 0" :data="nonErpTextList" style="width: 100%" max-height="450" id="table1" v-show="!canEdit">
      <el-table-column prop="name" label="">
      </el-table-column>
      <el-table-column prop="number" label="">
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import { numberCheck } from '@/utils/filter.js';
export default {
  name: 'my-non-erp-list',
  props: ['nonErpTextList'],
  data() {
    return {};
  },
  computed: {
    canEdit() {
      return this.$store.state.activityCanEdit;
    }
  },
  methods: {
    // 
    check(scope) {
      scope.row.number = numberCheck(scope.row.number);
    },
    addNonErpProject() {
      this.nonErpTextList.push({ name: '', number: '' });
      this.nonErpTextListChange();
    },
    handleDelete(scope) {
      this.nonErpTextList.splice(scope.$index, 1);
      this.nonErpTextListChange();
    },
    nonErpTextListChange() {
      this.$emit('nonErpTextListChange', this.nonErpTextList);
    }
  }
};
</script>



