<template>
  <div class="unionList">
    <p>ERP</p>
    <div class="addUnion" v-if="canEdit">
      <el-button type="primary" @click="addErpProject"></el-button>
      <span>ERPERP</span>
    </div>
    <!-- ERP  -->
    <div id="noUnion" v-if="erpTextList.length === 0">
      <img src="~assets/images/noCurrent.png">
      <p>
        
      </p>
    </div>
    <!-- ERP -->
    <el-table v-if="erpTextList.length > 0" :data="erpTextList" style="width: 95%" hight="450"  v-show="canEdit">
      <el-table-column prop="name" label="">
      </el-table-column>
      <el-table-column prop="number" label="">
        <template slot-scope="scope">
          <el-input v-model="scope.row.number" placeholder="" @keyup.native="check(scope)" @change="erpTextListChange"></el-input>
        </template>
      </el-table-column>
      <el-table-column prop="handle" label="" width="180">
        <template slot-scope="scope">
          <el-button size="small" @click="handleDelete(scope)"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-table v-if="erpTextList.length > 0" :data="erpTextList" style="width: 100%"  hight="450"  v-show="!canEdit">
      <el-table-column prop="name" label="">
      </el-table-column>
      <el-table-column prop="number" label="">
      </el-table-column>
    </el-table>
    <!--  ERP -->
    <div class="model_4">
      <el-dialog title="ERP" :visible.sync="visible" @close="resetData">
      <div>
        <el-row>
          <el-col style="width:220px;">
            <el-form :inline="true" class="demo-form-inline">
              <el-select v-model="erpType" clearable placeholder="" @change="erpModelChange">
                <el-option v-for="item in options1" :key="item.erpType" :label="item.erpName" :value="item.erpType">
                </el-option>
              </el-select>
            </el-form>
          </el-col>
          <el-col style="width:220px;">
            <el-form :inline="true" class="demo-form-inline">
              <el-select v-model="shopId" clearable placeholder="" @change="search">
                <el-option v-for="item in options2" :key="item.id" :label="item.name" :value="item.id">
                </el-option>
              </el-select>
            </el-form>
          </el-col>
          <el-col style="width:200px;">
            <div class="grid-content1 bg-purple">
              <el-input placeholder="" @keyup.enter.native="search" icon="search" v-model="input" :on-icon-click="search" class="input-search2 fl">
              </el-input>
            </div>
          </el-col>
        </el-row>
        <div class="section_ clearfix">
          <div style="float:left;width: 580px;height: 445px;">
            <el-table :data="tableData" style="width: 100%;height: 100%;" ref="multipleTable" @select="handleSelect" @select-all="handleSelectAll" @row-click="handleRowClick">
              <el-table-column type="selection" min-width="55px"></el-table-column>
              <el-table-column prop="name" label="ERP" min-width="100px">
              </el-table-column>
            </el-table>
            <el-pagination @current-change="handleCurrentChange" :current-page.sync="currentPage" :page-size="10" layout="prev, pager, next, jumper" :total="totalAll" v-if="tableData.length>0">
            </el-pagination>
          </div>
          <div class="rightContent">
            <p>{{ selectedErpRight.length }}</p>
            <p v-for="(item, index) in selectedErpRight" :key="item.id">
              <span> {{ item.name }} </span>
              <span>
                <el-input-number v-model="item.number" :min="1"></el-input-number>
              </span>
              <span>
                <el-button @click="handleDelete2(index)"></el-button>
              </span>
            </p>
          </div>
        </div>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="confirm"></el-button>
        <el-button @click="visible=false"></el-button>
      </span>
    </el-dialog>
    </div>
  </div>
</template>

<script>
import $http from '@/utils/http.js';
import { numberCheck } from '@/utils/filter.js';
export default {
  name: 'my-erp-list',
  props: ['erpTextList'],
  data() {
    return {
      visible: false,
      erpType: '',
      options1: [],
      shopId: '',
      options2: [],
      input: '',
      tableData: [],
      currentPage: 1,
      totalAll: 0,
      selectedErpRight: []
    };
  },
  computed: {
    canEdit() {
      return this.$store.state.activityCanEdit;
    }
  },
  methods: {
    handleDelete(scope) {
      this.erpTextList.splice(scope.$index, 1);
      this.erpTextListChange();
    },
    erpTextListChange() {
      this.$emit('erpTextListChange', this.erpTextList);
    },
    // 
    addErpProject() {
      $http
        .get(`/api/erp/list/erpType`)
        .then(res => {
          if (res.data.data) {
            this.options1 = res.data.data || [];
          } else {
            this.options1 = [];
          }
        })
        .catch(err => {
          this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 5000 });
        });
      this.visible = true;
    },
    // 
    erpModelChange() {
      if (this.erpType) {
        $http
          .get(`/api/shop/list`)
          .then(res => {
            if (res.data.data) {
              this.options2 = res.data.data || [];
            } else {
              this.options2 = [];
            }
          })
          .catch(err => {
            this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 5000 });
          });
      } else {
        this.options2 = [];
        this.shopId = '';
      }
    },
    // 
    search() {
      if (this.shopId) {
        $http
          .get(`/api/erp/list/server/${this.erpType}?current=1&shopId=${this.shopId}&search=${this.input}`)
          .then(res => {
            if (res.data.data) {
              this.tableData = res.data.data.records || [];
              this.totalAll = res.data.data.total;
            } else {
              this.tableData = [];
              this.totalAll = 0;
            }
          })
          .catch(err => {
            this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 5000 });
          });
      }
    },
    // 
    handleCurrentChange(val) {
      if (this.shopId) {
        $http
          .get(`/api/erp/list/server/${this.erpType}?current=${val}&shopId=${this.shopId}&search=${this.input}`)
          .then(res => {
            if (res.data.data) {
              this.tableData = res.data.data.records || [];
              this.$nextTick(() => {
                this.checkToggle();
              });
            } else {
              this.tableData = [];
            }
          })
          .catch(err => {
            this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 5000 });
          });
      }
    },
    // 
    handleRowClick(row) {
      this.$refs.multipleTable.toggleRowSelection(row);
      this.handleSelect(null, row, false);
    },
    // 
    handleSelect(selection, row, isAll) {
      let pass = this.selectedErpRight.some(item => {
        return item.id === row.id;
      });
      if (!pass) {
        this.selectedErpRight.push(row);
      } else {
        this.selectedErpRight.forEach((item, index) => {
          if (item.id === row.id && !isAll) {
            this.selectedErpRight.splice(index, 1);
          }
        });
      }
    },
    // 
    handleSelectAll(selection) {
      if (selection.length) {
        this.tableData.forEach(item => {
          this.handleSelect(null, item, true);
        });
      } else {
        this.tableData.forEach(item => {
          this.handleSelect(null, item, false);
        });
      }
    },
    // 
    checkToggle() {
      this.tableData.forEach((v, i) => {
        this.selectedErpRight.forEach(val => {
          if (v.id === val.id) {
            this.$refs.multipleTable.toggleRowSelection(this.tableData[i]);
          }
        });
      });
    },
    // 
    handleDelete2(index) {
      let deletRow = this.selectedErpRight.splice(index, 1);
      this.tableData.forEach((v, i) => {
        if (v.id === deletRow[0].id) {
          this.$refs.multipleTable.toggleRowSelection(this.tableData[i], false);
        }
      });
    },
    // 
    check(scope) {
      scope.row.number = numberCheck(scope.row.number);
    },
    // ERP
    confirm() {
      this.selectedErpRight.forEach(v => {
        this.erpTextList.push({
          erpType: this.erpType,
          shopId: this.shopId,
          erpTextId: v.id,
          name: v.name,
          number: v.number
        });
      });
      this.erpTextListChange();
      this.visible = false;
    },
    // 
    resetData() {
      this.erpType = '';
      this.tableData = [];
      this.selectedErpRight = [];
    }
  }
};
</script>



